@import"https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap";body{font-family:"Roboto",sans-serif;font-weight:300}.container{width:35rem;box-shadow:0 1px 1px 0 rgba(0,0,0,.1),0 3px 7px 0 rgba(0,0,0,.1)}.tasksContainer{box-shadow:0 -3px 5px rgba(0,0,0,.05);z-index:4;position:relative;width:100%}.taskForm{background:#fff;border-radius:4px;border-bottom:1px solid #ededed !important;display:flex;align-items:center}.taskForm .symbol{color:#dcdada;background-color:#fff;height:24px;padding:0 24px;text-align:center;font-weight:bold;font-size:18px;rotate:270deg;transition:all .2s}.taskForm .symbol:hover{color:#b9b9b9}.taskInput{width:33rem;font-family:"Roboto",sans-serif;font-weight:300;padding:1rem 0;border:none;font-size:1.5rem;font-style:italic;color:#4d4d4d;outline:none}.taskInput::placeholder{font-family:"Roboto",sans-serif;font-weight:300;color:#dcdada}.task{display:flex;align-items:center;border-bottom:1px solid #ededed;padding:1rem;font-size:1.5rem;margin:0;background:#fff}.task input[type=checkbox]{margin-right:1rem;width:24px;height:24px;border-radius:50%;border:1px solid #e5e5e5;appearance:none;outline:none;cursor:pointer;position:relative;background:#fff}.task input[type=checkbox]:checked::before{content:"âœ“";position:absolute;top:2px;left:5px;color:#5dc2af;font-weight:bold}.taskTitle{flex:1;color:#4d4d4d;user-select:none}.completed{color:#d9d9d9;text-decoration:line-through}.taskFormFooter{display:flex;justify-content:space-between;align-items:center;font-size:.9rem;color:#777;padding:1rem;background-color:#fff}.btnRow{display:flex;gap:.5rem}.btnRow button{padding:.3rem .7rem;border:1px solid rgba(0,0,0,0);border-radius:3px;background:#fff;cursor:pointer;font-size:.9rem;color:#777;transition:all .1s;font-weight:300}.btnRow button:hover{border-color:rgba(175,47,47,.1)}.btnRow button.active{border-color:rgba(175,47,47,.2)}.completedBtn{padding:.3rem .7rem;border:1px solid rgba(0,0,0,0);border-radius:3px;background:#fff;cursor:pointer;font-size:.9rem;color:#777;font-weight:300}.completedBtn:hover{border-color:rgba(175,47,47,.1)}.completedBtn.active{border-color:rgba(175,47,47,.2)}